// Поключение шрифтов
@import "fonts";

// Подключение библиотек
@import "libs";

// Подключение переменных
@import "vars";

// Подключение flex
@import "flex";

a {
	text-decoration-skip: ink;
	outline: 0; }

body {
	margin: 0;
	padding: 0;
	font-family: $default-font;
	color: $color-accent; }

img {
	width: 100%;
	height: auto; }

//
// Всегда подключается в конце
//
@import "media";
